import React from 'react';
import { useNavigate } from 'react-router-dom';

function HomePage() {
    const navigate = useNavigate();

    const handleLoginClick = () => {
        navigate('/login');
    };

    const handleSignUpClick = () => {
        navigate('/signUp');
    };

    const handleMainPageClick = () => {
        navigate('/mainPage');
    };

    return (
        <div>
            <h1></h1>
            <p> </p>
            <button type="button" onClick={handleLoginClick}></button>
            <button type="button" onClick={handleSignUpClick}></button>
            <div>
            <button type="button" onClick={handleMainPageClick}> </button>
            </div>
        </div>
    );
}

export default HomePage;
