import React, { useState, useEffect } from 'react';
import '../../styles/Main/MainPage.css';
import { fetchCurrentMember } from '../../services/TokenService.jsx';
import CalendarComponent from '../../components/commons/CalendarComponent.jsx';
import NavBar from '../../components/commons/NavBar.jsx';

function MainPage() {
    const [memberName, setMemberName] = useState('');

    useEffect(() => {
        const token = localStorage.getItem('accessToken'); //      

        if (token) { 
            const getMemberData = async () => {
                const memberData = await fetchCurrentMember(token); //    
                if (memberData && memberData.name) { //     
                    setMemberName(memberData.name); //  
                } else {
                    console.error('    ');
                }
            };

            getMemberData();
        } else {
            console.error(' ');
        }
    }, []);

    return (
        <div className="main-page">
            <NavBar title="" />
            <h1>, {memberName}!</h1>
            <CalendarComponent />
        </div>
    );
}

export default MainPage;
