import React, { useState } from 'react';
import axios from 'axios';
import '../../styles/Friend/FriendSearch.css';
import FriendRequest from './FriendRequest.jsx'
import defaultProfilePic from '../../assets/DefaultImage.png'

const API_BASE_URL = process.env.REACT_APP_API_URL;

const FriendSearch = () => {
    const [keyword, setKeyword] = useState('');
    const [loading, setLoading] = useState(false);
    const [searchResults, setSearchResults] = useState([]);
    const [error, setError] = useState('');

    const handleSearch = async () => {
        if (!keyword.trim()) {
            setError('   .');
            return;
        }

        setLoading(true);
        setError('');

        try {
            const token = localStorage.getItem('accessToken');
            const response = await axios.get(`${API_BASE_URL}/friends/search`, {
                params: { keyword },
                headers: {
                    'Authorization': `${token}`,
                },
            });

            if (response.data && Array.isArray(response.data.content)) {
                setSearchResults(response.data.content);
            } else {
                setSearchResults([]);
            }
        } catch (err) {
            setError('    ');
            console.error(err);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="searchBox">
            <div className="searchContent">
                <input
                    type="text"
                    placeholder="  "
                    value={keyword}
                    onChange={(e) => setKeyword(e.target.value)}
                />
                <button onClick={handleSearch} className="search-button" disabled={loading}>
                    {loading ? ' ...' : ''}
                </button>
            </div>
            {error && <div className="error-message">{error}</div>}
            <div className="search-results">
                {searchResults.length > 0 ? (
                    <ul>
                        {searchResults.map((member) => (
                            <li key={member.id} className="search-result-item">
                                <div className="search-result-info">
                                    <span>{member.name}</span>
                                    <img
                                        src={member.image || defaultProfilePic}
                                        alt={`${member.name} `}
                                        className="profile-image"
                                    />
                                </div>
                                <button
                                    className="send-request-button"
                                    onClick={() => FriendRequest(member.id)}
                                >
                                     
                                </button>
                            </li>
                        ))}
                    </ul>
                ) : (
                    !loading && <p>  .</p>
                )}
            </div>
        </div>
    );
};

export default FriendSearch;